#about {
  margin: 0px 150px;
  height: 100vh;
  padding-top: 50px;
}

#about .about-main {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 70px;
}

#about .about-main img {
  width: 300px;
  border-radius: 15px;
}

#about .about-main div:first-child {
  width: 50%;
  font-size: 20px;
  color: var(--prim-col2);
}
