.project-card {
  display: flex;
  flex-direction: row;
  /* border: 1px solid red; */
  justify-content: space-between;
  margin-top: 50px;
  margin-bottom: 20px;
}

.project-card .project-image {
  width: 48%;
  /* border: 2px solid green; */
}
.project-card .project-image img {
  width: 100%;
}

.project-card > div:last-child {
  width: 48%;
  /* border: 2px solid blue; */
}
.project-card > div:last-child .project-title {
  color: var(--prim-col2);
}
.project-card > div:last-child .project-description,
.project-card > div:last-child .project-tech-stack {
  color: var(--prim-col22);
}
.project-card > div:last-child > div {
  margin-top: 15px;
}
.project-card > div:last-child > div:last-child {
  display: flex;
  margin-top: 50px;
}
