#contact {
  margin: 0 150px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 200vh;
  padding-top: 50px;
  color: var(--prim-col22);
}
.headline {
  margin-bottom: 70px;
}
#contact > div {
  width: 40%;
  /* border: 1px solid red; */
}
#contact > div:first-child p {
  margin-bottom: 50px;
}
input,
textarea {
  margin-bottom: 10px;
  color: white;
  background-color: var(--prim-col11);
  border: none;
}
input:focus,
textarea:focus {
  outline: none;
}
textarea {
  height: 80px;
  resize: none;
}
#contact > div:last-child > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#contact > div:first-child > div:last-child div:first-child input {
  width: calc(50% - 45px);
  padding: 18px;
}
#contact > div:first-child > div:last-child div:first-child input:first-child {
  margin-right: 10px;
}
#contact > div:first-child > div:last-child div:nth-child(2) input {
  width: calc(100% - 40px);
  padding: 18px;
}
#contact > div:first-child > div:last-child div:nth-child(3) textarea {
  width: calc(100% - 40px);
  padding: 18px;
}

.contact-links > div:last-child {
  /* border: 1px solid red; */
  display: fle;
  flex-direction: column;
  /* align-items: center; */
  font-size: 20px;
  height: 500px;
  justify-content: center;
}
.contact-links > div:last-child div {
  margin-bottom: 5px;
  color: var(--prim-col2);
}
.contact-links > div:last-child a {
  text-decoration: none;
}
